import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { AppConfigurationProvider } from '../providers/configuration/app-configuration';
import { CustomHttpProvider } from '../providers/configuration/custom-http';
import { HttpModule } from '@angular/http';
import { MyApp } from './app.component';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { FormsModule } from '@angular/forms';
// import { CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';

import { RevmaxProvider } from '../providers/revmax';
import { WooApiModule } from 'ng2woo';
import { IonicStorageModule } from '@ionic/storage';

import { Ionic2RatingModule } from 'ionic2-rating';

// import { VideoPlayer } from '@ionic-native/video-player';
const WooCommerceConfig = {
  url:   'https://revmax.twinspark.co', 
  consumerKey:    'ck_5ecf43a297b5341dfb68c4ba5f7e83db56125b19',
  consumerSecret:  'cs_6387cb6a55c87e8cd6223fbca39a92324dbfd013',
  wpAPI: true,
  version: 'wc/v1',
  queryStringAuth : true
};

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/cart/cart.module.ngfactory#CartPageModuleNgFactory', name: 'cart', segment: 'cart', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/dashboard/dashboard.module.ngfactory#DashboardPageModuleNgFactory', name: 'dashboard', segment: 'dashboard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/helpful-links/helpful-links.module.ngfactory#HelpfulLinksPageModuleNgFactory', name: 'helpful-links', segment: 'helpful-links', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'product-category', segment: 'product-category/:slug/:catId', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/installation-instructions/installation-instructions.module.ngfactory#InstallationInstructionsPageModuleNgFactory', name: 'installations', segment: 'installations', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list/list.module.ngfactory#HomePageModuleNgFactory', name: 'ListPage', segment: 'list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/product-detail/product-detail.module.ngfactory#ProductDetailPageModuleNgFactory', name: 'product-detail', segment: 'product-detail/:id', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/search-products/search-products.module.ngfactory#SearchProductsPageModuleNgFactory', name: 'search-products', segment: 'search-products', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/searched-results/searched-results.module.ngfactory#SearchedResultsPageModuleNgFactory', name: 'searched-results', segment: 'searched-results', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/upgrade-products/upgrade-products.module.ngfactory#UpgradeProductsPageModuleNgFactory', name: 'upgrade-products', segment: 'upgrade-products', priority: 'low', defaultHistory: [] }
  ]
}),
    HttpModule,
    WooApiModule.forRoot(WooCommerceConfig),
    IonicStorageModule.forRoot(),
    Ionic2RatingModule,
    FormsModule, // Put ionic2-rating module here
  ],
  // schemas: [
  //   CUSTOM_ELEMENTS_SCHEMA
  // ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AppConfigurationProvider,
    CustomHttpProvider,
    RevmaxProvider,
    WooApiModule,
    // VideoPlayer,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
  ]
})
export class AppModule {}



